html,body{
overflow-x: hidden;
}
body{
  overflow:hidden;
}
.App {
  background-color: #282c34;
  min-height: 100vh;
  color:white;
}
a{
  color: white;
}
.App-link {
  color: #61dafb;
}

div[class^="bet-"] {
  min-height: 200px;
  border:5px solid black;
  display: flex;
}

div[class^="bet-"]  h1{
  font-size:20px;
  text-align:center;
}

.bet-0{
  background: rgb(163, 89, 28);
}

.bet-1{
  background: rgb(163, 28, 28);
}

.bet-2{
  background: rgb(17, 98, 98);
}

.qrholder,.infoholder{
  float:left;
}

.qrholder{
  height:200px;
}

.infoholder{
  margin-left:20px;
  margin-right:20px;
  min-width: 300px;
  height: 100%;
  flex-grow: 1;
}
.betnowbutton{
  text-align: right;
}
.betnow{
  float:right;
}

.betnow:hover .betnowhidden,.betnow:active .betnowhidden{
  height:auto;
  opacity:1;
}

.betnowhidden{
  height:0;
  opacity:0;
  transition:0.5s linear;
}


@media only screen and (max-width: 600px) {
  div[class^="bet-"] {
    flex-direction: column;
  }
  .qrholder, .infoholder{
    float:none;
  }
  .qrholder{
    display: table;
    margin: 0 auto;
  }
  div[class^="bet-"] h1{
    font-size:16px;
    word-wrap: break-word;      /* IE 5.5-7 */
    white-space: -moz-pre-wrap; /* Firefox 1.0-2.0 */
    white-space: pre-wrap;      /* current browsers */
  }
  div[class^="bet-"] {
    padding:10px;
  }
}